@import url(https://fonts.googleapis.com/css?family=Roboto:300);

.login-page {
  display: flex;
  flex-flow: row;
  justify-content: space-evenly;
  align-items: center;
  width: 60%;
  padding: 8% 0 0;
  margin: auto;
}

.faceMe-intro {
  color: wheat;
  margin-top: -50px;
}

.form {
  position: relative;
  z-index: 1;
  border-radius: 5px;
  background: rgba(245, 222, 179, 0.5);
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form input {
  border-radius: 5px;
  font-family: 'Roboto', sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.form button {
  border-radius: 5px;
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #cc421f;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #ffffff;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form button:hover,
.form button:active,
.form button:focus {
  background: #993117;
}
.form .message {
  margin: 15px 0 0;
  color: wheat;
  font-size: 12px;
}
.form .message span {
  color: #cc421f;
  text-decoration: none;
  cursor: pointer;
}

.container {
  position: relative;
  z-index: 1;
  max-width: 300px;
  margin: 0 auto;
}
.container:before,
.container:after {
  content: '';
  display: block;
  clear: both;
}
.container .info {
  margin: 50px auto;
  text-align: center;
}
.container .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #1a1a1a;
}
.container .info span {
  color: #4d4d4d;
  font-size: 12px;
}
.container .info span a {
  color: #000000;
  text-decoration: none;
}
.container .info span .fa {
  color: #ef3b3a;
}
body {
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
