@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(131, 58, 180);
  background: linear-gradient(
    90deg,
    rgba(131, 58, 180, 1) 0%,
    rgba(224, 77, 77, 1) 50%,
    rgba(252, 176, 69, 1) 100%
  );
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

header {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  align-items: center;
}

.navigation {
  text-align: right;
}

.nav-link {
  padding: 3px;
  font-size: x-large;
  text-decoration: underline;
  color: lightgray;
  border-radius: 5px;
  padding: 5px;
  color: black;
  transition: all 0.2s ease-in-out;
}

.nav-link:visited {
  color: black;
}
.nav-link:hover {
  cursor: pointer;
  background-color: rgb(101, 53, 101);
  border-color: rgb(101, 53, 101);
}
.nav-link:focus {
  color: black;
}
.nav-link:hover,
.nav-link:active {
  color: lightgray;
}

.Tilt {
  display: flex;
  justify-content: center;
  align-items: center;
}

.rank {
  display: flex;
  flex-flow: column;
  align-items: center;
}

.rank-text-2 {
  font-size: 30px;
}

.image-form {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  align-self: center;
  text-align: center;
  border-radius: 5px;
  width: 35%;
  max-width: 360px;
  background: rgba(245, 222, 179, 0.5);
  padding: 45px;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

.input-box {
  display: flex;
  justify-content: center;
}

.text-input {
  width: 70%;
}

.scan-button {
  border-radius: 5px;
  width: 25%;
  outline: none;
  margin: 5px;
  transition: all 0.2s ease-in-out;
  text-transform: uppercase;
  outline: 0;
  background: #cc421f;
  border: 0;
  padding: 10px;
  color: #ffffff;
  font-size: 14px;
  cursor: pointer;
}

.scan-button:hover {
  background-color: rgb(101, 53, 101);
  border-color: rgb(101, 53, 101);
  color: lightgray;
  cursor: pointer;
}

.face-rec {
  display: flex;
  position: relative;
  margin-top: 5%;
  justify-content: center;
  align-self: center;
  width: 500px;
}

.bounding_box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}

.login-page {
  display: flex;
  flex-flow: row;
  justify-content: space-evenly;
  align-items: center;
  width: 60%;
  padding: 8% 0 0;
  margin: auto;
}

.faceMe-intro {
  color: wheat;
  margin-top: -50px;
}

.form {
  position: relative;
  z-index: 1;
  border-radius: 5px;
  background: rgba(245, 222, 179, 0.5);
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form input {
  border-radius: 5px;
  font-family: 'Roboto', sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.form button {
  border-radius: 5px;
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #cc421f;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #ffffff;
  font-size: 14px;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form button:hover,
.form button:active,
.form button:focus {
  background: #993117;
}
.form .message {
  margin: 15px 0 0;
  color: wheat;
  font-size: 12px;
}
.form .message span {
  color: #cc421f;
  text-decoration: none;
  cursor: pointer;
}

.container {
  position: relative;
  z-index: 1;
  max-width: 300px;
  margin: 0 auto;
}
.container:before,
.container:after {
  content: '';
  display: block;
  clear: both;
}
.container .info {
  margin: 50px auto;
  text-align: center;
}
.container .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #1a1a1a;
}
.container .info span {
  color: #4d4d4d;
  font-size: 12px;
}
.container .info span a {
  color: #000000;
  text-decoration: none;
}
.container .info span .fa {
  color: #ef3b3a;
}
body {
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

